
import vButtom from '../components/vButtom'
import Vue from 'vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

Vue.use(DatetimePlugin)
export default {
components: { Actionsheet, vButtom },
data() {
return {
userName: '',
telephone: '',
birthday: '',
cardName: '',
show1: false,
cardList: ['储值卡', '计次卡'],
loading: false
}
},
methods: {
selectDate(val) {
let vm = this
this.$vux.datetime.show({
value: vm.birthday,
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD',
yearRow: '{value}年',
monthRow: '{value}月',
dayRow: '{value}日',
startDate: '1900-01-01',
onConfirm(val) {
vm.birthday = val
}
})
},
selectCard(key, val) {
this.cardName = val
},
onSubmit() {
if (!this.userName) {
this.$toast({msg: '请输入客户名字'})
return
}
this.loading = true
setTimeout(() => {
this.loading = false
this.$toast({msg: '提交成功', type: 'success'})
}, 1000)
}
}
}



