export default {
  data() {
    return {
      // txt: '提交'
    }
  },
  props: {
    txt: {
      type: String
    },
    count: {
      type: Number,
      default: 1
    },
    rightTxt: {
      type: String
    },
    leftTxt: {
      type: String
    },
    loading: {
      type: Boolean,
      default: false
    },
    isBottom: {
      type: Boolean,
      default: true
    },
    round: {
      type: Boolean,
      default: false
    },
    simple: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    submit() {
      this.$emit('onSubmit', '')
    },
    onLeft() {
      this.$emit('onLeft', '')
    },
    onRight() {
      this.$emit('onRight', '')
    }
  }
}



